/* Estilos para el componente PostImage */
.PostImage-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    background-color: #2c3e50;  /* Fondo azul oscuro */
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
    margin: 20px auto;
    max-width: 600px;
    min-height: 600px;
    padding-bottom: 60px;
}

.PostImage-container h2 {
    color: #9b59b6;  /* Púrpura vibrante */
    margin-top: 40px;
    margin-bottom: 20px;
}

.PostImage-container h3 {
    color: #1abc9c;  /* Verde agua */
    margin-bottom: 30px;
}

.PostImage-container img {
    height: 400px;
    width: 500px;
    border-radius: 8px;
    margin-bottom: 20px;
}

.PostImage-container button {
    padding: 15px 20px;
    background-color: #3498db;  /* Azul claro vibrante */
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.PostImage-container button:hover {
    background-color: #2980b9;  /* Azul más oscuro al hacer hover */
}


