/* Estilos para el componente GetSearchImage */
.GetSearchImage-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: #2c3e50;  /* Fondo azul oscuro */
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
  margin: 20px auto;
  max-width: 600px;
  min-height: 600px;
  margin-bottom: 60px;
  padding-bottom: 50px;
}

.GetSearchImage-container h2 {
  color: #9b59b6;  
  margin-top: 40px;
  margin-bottom: 20px;
}

.GetSearchImage-container h3 {
  color: #1abc9c;  
  margin-bottom: 30px;
}

.GetSearchImage-container img {
  height: 300px;
  width: 300px;
  border-radius: 8px;
  margin-bottom: 20px;
}

.GetSearchImage-container button {
  padding: 10px 20px;
  background-color: #3498db;  /* Azul claro vibrante */
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-bottom: 50px;
}

.GetSearchImage-container button:hover {
  background-color: #2980b9;  /* Azul más oscuro al hacer hover */
}

.GetSearchImage-container input {
  padding: 10px;
  margin: 10px;
  width: 80%;
  border: 1px solid #3498db;  /* Borde azul */
  border-radius: 5px;
  background-color: #3a3f44;
  color: white;
}

  